body {
  font-family: 'Noto Sans', sans-serif;
  background-color: #4b4b4b;
  color: #fff;
  font-size: 15.7px;    
  line-height: 1.4;
  width: 100vw;
  height: 100%;
  overflow-x: hidden;
}
* {
  margin: 0;
}
a {
  text-decoration: none;
  color: #fff;
}
.link {
  color: #2332a0;
  text-decoration: underline;
}
article,section {
  display: flex;
  width: 100%;
  flex-direction: column;
  padding: 0;
}
header {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100vw;
  justify-content: center;
  padding-top: calc(16px*0.95);
}
.header {
  display: flex;
  align-items: center;
  padding: calc(4px*0.95);
  height: calc(2.25rem*0.95);
  background-color: #292929;
  border-radius: calc(1rem*0.95);
  border: 1px solid #54535f;
  font-size: calc(16px*0.95);
  box-shadow: 0 0 #0000,0 0 #0000,0 0 #0000,0 0 #0000,0 20px 25px -5px rgba(0,0,0,.1),0 8px 10px -6px rgba(0,0,0,.1);
}
.header a {
  padding: 0 calc(12px*0.95);
  display: flex;
  justify-content: center;
  height: fit-content;
}
.header a:hover {
  opacity: .8;
}
h1,h2 {
  color: #fff;
  font-family: 'Extenda',Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-weight: 400;
  font-size: 60px;
  margin-bottom: 15px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.4);
}
h1:not(.juegos h1):not(.contacto h1) {
  margin: 0;
  font-size: 100px;
  line-height: 1;
  margin-bottom: 20px;
}
.juegos h1, .contacto h1 {
  color: #fff;
  padding: 10px;
}
#articulo h1 {
  color: #311f19;
  font-size: 60px;
}
.inicio-lf {
  background-image: url("../../fondo-section-lf.png");
  background-size: cover;
  color: #fff;
  height: 105vh;
  background-position: top;
  justify-content: end;
}
.inicio-lf__about,.trucouy__about {
  width: 335px;
  margin-bottom: 5px;
}
.inicio-lf__block1 {
  padding-left:calc(97.4026px*0.62);
  padding-bottom: 13vh;
}
.trucouy__block1 {
  padding-top: calc(78.9199px*0.62);
  padding-left:calc(97.4026px*0.62);
}
button,
.button,
.inicio-lf__btn1,
.inicio-lf__btn2,
.contacto__input--submit,
.page-not-found__btn-home,
.articulo__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #2332a0;
  border: none;
  color: #fff;
  text-transform: uppercase;
  width: calc(calc(768.52px * 0.328822)*0.62);
  height: calc(calc(256px * 0.328822)*0.62);
  border-radius: 13.5px;
  margin: 15px 5px;
  transition: color .15s,background-color .15s;
}
button:hover,
.inicio-lf__btn1:hover,
.inicio-lf__btn2:hover,
.page-not-found__btn-home {
  background-color: rgba(35, 50, 160,.8);
}
.inicio-lf__block2 {
  display: flex;
}
.inicio-lf__btn1 {
  margin-left: 0;
}
.inicio-lf__btn2 {
  background-color: #fff;
  color: #2332a0;
}
.inicio-lf__btn2:hover {
  color: #fff;
  background-color: #2332a0;
}
.header .header__icon {
  width: calc(2.25rem*0.95);
  height: calc(2.25rem*0.95);
  padding: 0;
  border-radius: calc(.75rem*0.95);
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__icon img {
  height: 65%;
}
.inicio-lf__tag,
.trucouy__tag,
.articulo__tag,
.articulo__tag-1 {
  color: #fff6;
  margin-bottom: 7px;
  position: relative;
  top: -10px;
  text-transform: uppercase;
}
.trucouy__tag {
  color: #aaa;
}
.articulo__tag,
.articulo__tag-1 {
    color: #0006;
}

.articulo__tag-1 {
    color: #311f19;
}

.articulo__tag-1:hover {
    text-decoration: underline;
    color: #311f19;
}
.imagen-clasic {
  object-fit: cover;
  width: 600px;
  height: 400px;
  margin: 10px 0;
}
iframe {
  border: none;
  border-radius: 5px;
  width: calc(calc(1199px * 0.559965)*0.62);
  height: calc(calc(837.493px * 0.559965)*0.62);
  margin: 20px 0;
}
.trucouy {
  flex-direction: row;
  padding-bottom: 50px;
}
.trucouy__img {
  height: 450px;
  margin-bottom: 60px;
}
.trucouy__block2 {
  display: flex;
  align-items:flex-end;
  justify-content: center;
  width: 100%;
}
.contacto__form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.contacto__input,
.contacto__textarea {
  font-family: 'Noto Sans', sans-serif;
  border-radius: 10px;
  background-color: #fff;
  border: 1.5px solid #ecedec;
  color: #000;
  height: 50px;
  padding-left: 10px;
  margin: 5px 0;
  width: 300px;
}
.contacto__textarea {
  padding-top: 10px;
  max-width: 300px;
}
.novedades, .articulo, .juegos {
  padding-top: calc(78.9199px*0.62);
  padding-left:calc(97.4026px*0.62);
  background-color: #353535;
  padding-bottom: 50px;
}
.juegos {
  padding-top: 13vh;
  background-color: #353535;
}
.articulo {
  padding-top: calc(160px*0.62);
  padding-right: 5vw;
  width: initial;
  background-color: #ecedec;
  color: #2c2c2c;
  line-height: 28px;
  font-size: 16px;
  min-height: 100vh;
}
.articulo h2 {
  margin-top: 48px;
  margin-bottom: 32px;
  color: #2c2c2c;
  font-size: 28px;
  font-family: 'Noto Sans', sans-serif;
  font-weight: bolder;
}
.articulo p {
  margin: 0;
  padding: 0;
}
.contacto {
  display: flex;
  background-color: #353535;
  height: 105vh;
}
.contacto__block1 {
  padding-left: calc(97.4026px* 0.62);
  padding-top: 13vh;
}
.page-not-found__block1 {
  padding-left: calc(97.4026px* 0.62);
  padding-top: 13vh;
}
.novedades img ,
.juegos img{
  border-radius: 5px;
  width: 100%;
  height: calc(calc(650px* 0.559965)* 0.62);
  margin: 20px 0;
  margin-bottom: 10px;
  object-fit: cover;
}
.juegos img{
  height: calc(calc(850px* 0.559965)* 0.62);
}
.novedades__noticias {
  display: flex;
  flex-wrap: wrap;
  gap: 1%;
}
.novedades__articulo {
  width: 28.5%;
}
.novedades__articulo--juego {
  min-width: calc(40% - 120px);
  border-radius: 5px;
  padding: 20px;
  border: 1.5px solid #5e5d5e;
  background-color: #4b4b4b;
  color: #fff;
}
.novedades__articulo--juego:hover {
  opacity: 0.8;
}
.novedades__articulo p{
  margin-bottom: 10px;
}
.novedades__titulo--juego {
  font-size: 23px;
  line-height: 1.4;
  margin: 0;
  height: 30px;
  overflow-y: hidden;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 75px;
}

@font-face {
  font-family: 'Extenda';
  src: url("../../Extenda-80-Peta.ttf");
}
@font-face {
  font-family: 'Noto Sans';
  src: url("../../NotoSans-Regular.ttf");
}